@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;300;400;500;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Roboto+Slab:wght@200;300;400;500;700;800;900&display=swap);

.App {
  text-align: center;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.banner{
    background: linear-gradient(rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.7)), url('/assets/banner/yacimiento.jpeg') center;
    background-size: cover;
    width: 100%;
    padding: 18vh 10vw;
}

.title-banner{
    font-family: 'Roboto Slab', serif;
    color: #00ACBB;
    font-size: 15vw;
}

.subtitle-banner{
    font-family: 'Roboto Slab', serif;
     font-size: 7vw;
     color: #fff;
     font-weight: 200;
}

#inicio .btn-secondary{
    background-color: #00ACBB;
    border: none;
    padding: 0.75rem 2rem;
}


@media (min-width: 768px){
    
    .title-banner{
        font-size: 12vw;
    }

    .subtitle-banner{
        font-size: 4.5vw;
    }
    

@media (min-width: 1024px){

    .banner{
        width: 100%;
        padding: 14vh 10vw;
    }
    
    .title-banner{
        font-size: 7vw;
    }

    .subtitle-banner{
         font-size: 3vw;
    }

}
}

.container-footer{
    display: flex;
    flex-direction: column;
    padding-top: 1.5rem;
    padding-bottom: 1rem;
}

a{
    list-style: none;
    text-decoration: none;
    color: #1d6b87;
}

.footer-title{
    font-size: 25px;
    color: #1d6b87;
}

.footer-container-lista,
.footer-container-iconos{
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 0.5rem 0px;
}

.footer-lista,
.footer-iconos{
    padding: 0px 0.35rem;
    font-family: 'Lato', sans-serif;
    color: #565e66;
    cursor: pointer;
}

.footer-iconos{
    font-size: 1.5rem;
}

.footer-copyright{
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    text-align: center;
    margin-bottom: 1.5rem;
}

@media (min-width: 1024px){
   
    .container-footer{
        flex-direction: row;
        justify-content: space-around;
    }

    .footer-lista,
    .footer-iconos{
        padding: 0px 0.75rem;
    }
}




.bg-light{
    background-color: #f0f7f7;
}

.navbar-light .navbar-brand {
    color: #1d6b87;
    font-size: 22px;
    font-weight: 500;
}

.navbar-light .navbar-brand:hover {
    color: #1d6b87;
}

.navbar-light .navbar-nav .nav-link {
    color: #1d6b87;
    font-size: 20px;
    padding: 0.30rem 2rem;
}

.navbar-light .navbar-nav .nav-link:hover {
    color: #1d6b87;
}
.container-certificacion{
    background-color: #dfeeee;
    -webkit-filter: brightness(99%);
            filter: brightness(99%);
}

.titulo-certificacion {
    font-size: 1.7rem;
    font-weight: 300;
    font-family: 'Roboto Slab', serif;
    color: #424A4F;
    line-height: 1.3;
    padding: 2rem 0px;
}

.img-certificacion{
    width: 35%;
    max-width: 400px;
    margin: 0rem 2rem 3rem 2rem;
}

#img-iram{
    opacity: 0.75;
}


.compra-bentonita{
    background-color: #dfeeee;
    padding: 2rem;
}

.compra-bentonita-title{
    font-size: 38px;
    color: #424A4F;
    font-family: 'Roboto Slab', serif;
    font-weight: 300;
    margin-bottom: 1rem;
}

.compra-bentonita-data{
    font-family: 'Lato', sans-serif;
    margin-bottom: 1rem;
    color: #424A4F;
}

.button-compra-bentonita{
    align-items: center;
    margin-top: 1.25rem;
}

#formulario{
    display: flex;
    flex-direction: column;
    margin-right: 0.75rem;
}

@media (min-width: 1024px){

    .container-compra-bentonita{
        margin: 0rem 1rem;
    }

     .compra-bentonita-title{
        font-size: 3rem;
    }
    
     .compra-bentonita-data{
        font-size: 1.15rem;
    } 
 
    .container-compra-bentonita{
        display: flex;
    }

    .texto-compra-bentonita,
    .formulario-compra-bentonita{
        padding: 1.5rem 3rem;
    }

    #formulario{
        margin-right: 0rem;
    }
}



@media (min-width: 1280px){

    .compra-bentonita-title{
        margin-top: 1rem;
        font-size: 3.5rem;
    }
    
     .compra-bentonita-data{
         margin-top: 2rem;
        font-size: 1.5rem;
    } 
}
.compra-productos{
    background-color: #f0f7f7;
    padding: 2rem;
}

.container-compra-productos{
    display: flex;
    flex-direction: column;
}

.compra-productos-title{
    font-size: 38px;
    color: #424A4F;
    font-family: 'Roboto Slab', serif;
    font-weight: 300;
    margin-bottom: 1.5rem;
}

.compra-productos-data{
    font-family: 'Lato', sans-serif;
    margin-bottom: 1rem;
    color: #424A4F;
}

.button-compra-productos{
    margin-top: 2rem;
}

.btn-secondary{
    background-color: #FF8C1E;
    border: none;
}

.btn-secondary:hover{
    background-color: #DC5572;
}

#video-rubicat{
    width: 80vw;
    border-radius: 10px;
    margin: auto;
}

@media (min-width: 768px){
    .container-video-boton{
        display: flex;
        flex-direction: column;
    }
}

@media (min-width: 1024px) {

    .container-compra-productos{
        display: flex;
        flex-direction: row;
       /*  padding: 2rem -2rem;  */
        margin: 0px 0rem;
    }
    
    .container-data-general,
    .container-video-boton{
        padding: 1rem 4rem;
        width: 50%;
    }

    .compra-productos-title{
       font-size: 3rem;
    }
    .compra-productos-data{
        font-size: 1.15rem;
    }

