        padding: 0px 3rem;
        margin-bottom: 1rem;
        margin-right: 2.5rem;
    }

    #video-rubicat{
        width: 40vw;
        margin-top: 1rem;
    }

}

@media (min-width: 1280px){

    .container-data-general,
    .container-video-boton{
        padding: 1.5rem 0rem;
    }

    .compra-productos-title{
        margin-top: 1rem;
        font-size: 3.5rem;
    }
    
     .compra-productos-data{
         margin-top: 2rem;
        font-size: 1.5rem;
    } 

    .container-video-boton{
        padding: 0px 3rem;
        margin-bottom: 1rem;
        margin-right: 0rem;
    }
}
.texto-explicativo-azul{
    color: #424A4F;
